.clearfix:before,
.clearfix:after,
.container:before,
.container:after,
.container-fluid:before,
.container-fluid:after,
.row:before,
.row:after {
  content: " ";
  display: table;
}
.clearfix:after,
.container:after,
.container-fluid:after,
.row:after {
  clear: both;
}
.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.invisible {
  visibility: hidden;
}
.text-hide {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}
.hidden {
  display: none !important;
}
.affix {
  position: fixed;
}
.nav {
  padding: 3rem 0;
}
.nav__logo a {
  border: none;
}
.nav__logo a:hover {
  border: none;
}
.nav svg {
  margin: .5rem 0;
}
.nav__menu__item ul {
  margin: 0;
  padding: 0;
}
article {
  color: #000;
}
.accent,
.accent a {
  color: #0000ff;
}
.bg {
  background-color: #fff;
}
.image__overlay {
  overflow: hidden;
  height: 70%;
}
.contact__item {
  margin: 3em 0;
}
.contact__item a {
  display: block;
  float: left;
  color: #0000ff;
  padding: 1rem 2rem;
  border: 1px solid #0000ff;
  margin-left: -1px;
  margin-top: -1px;
}
.contact__item a:hover {
  color: #fff;
  background-color: #0000ff;
}
.btn {
  color: #0000ff;
  padding: 1rem 2rem;
  border: 1px solid #0000ff;
  cursor: pointer;
  width: 20%;
}
.btn:hover {
  color: #fff;
  background-color: #0000ff;
}
input {
  display: block;
  font-size: 1em;
  color: #000;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #0000ff;
  box-sizing: border-box;
  width: 100%;
  max-width: 500px;
  padding: .5em 0;
  margin-top: 1em;
  position: relative;
  border-radius: 0;
  outline: none;
}
input::-webkit-input-placeholder {
  color: #000;
}
textarea {
  display: block;
  font-size: 1em;
  color: #000;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #0000ff;
  box-sizing: border-box;
  width: 100%;
  max-width: 500px;
  padding: .5em 0;
  margin-top: 1em;
  position: relative;
  border-radius: 0;
  outline: none;
}
textarea::-webkit-input-placeholder {
  color: #000;
}
.st0 {
  fill: #111;
  cursor: pointer;
  transform: scale(1);
  transform-origin: center;
  transition: 500ms cubic-bezier(0, 0, 0.2, 1);
}
.st0:hover {
  stroke: transparent;
  transform: scale(0.9);
  transition: 500ms cubic-bezier(0, 0, 0.2, 1);
}
.st0:active {
  stroke: transparent;
  transform: scale(0.8);
  transition: 200ms cubic-bezier(0, 0, 0.2, 1);
}
.first:active {
  fill: #fbf18e;
}
.second:active {
  fill: #ff8d6c;
}
